'use client'

import { useState } from 'react';
import { FaFolder, FaFile, FaClock, FaCalendar } from 'react-icons/fa';

interface WritingFile {
  id: string;
  title: string;
  date: string;
  content: string;
  folder: string;
  sticker?: string;
}

const writings: { [key: string]: WritingFile[] } = {
  'Quotes': [
    {
      id: 'quote1',
      title: 'The Power of Silence',
      date: '2022-13-June',
      content: 'The WORLD is wortless piece filled with meaningful PEACE',
      folder: 'Quotes',
      sticker: 'üåü'
    },
    {
      id: 'quote2',
      title: 'Embracing Change',
      date: '2024-01-02',
      content: 'Like seasons that shift...',
      folder: 'Quotes',
      sticker: 'üçÅ'
    },
    // Add more quotes...
  ],
  'Poetry': [],
  'Stories': [],
  'Essays': [],
  'Reflections': [],
  'Journal': []
};

export default function WritingsPage() {
  const [selectedFolder, setSelectedFolder] = useState<string | null>(null);
  const [selectedFile, setSelectedFile] = useState<WritingFile | null>(null);

  return (
    <main className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8 text-center">Writings</h1>

      {/* Content Display */}
      <div className="grid grid-cols-1 md:grid-cols-12 gap-6">
        {/* Folders Panel */}
        <div className="md:col-span-3 bg-white/80 backdrop-blur-sm rounded-xl shadow-lg p-4">
          <h2 className="text-lg font-semibold mb-4">Folders</h2>
          <ul className="space-y-2">
            {Object.keys(writings).map((folder) => (
              <li key={folder}>
                <button
                  onClick={() => {
                    setSelectedFolder(folder);
                    setSelectedFile(null);
                  }}
                  className={`w-full flex items-center gap-3 p-2 rounded-lg transition-all duration-200 ${
                    selectedFolder === folder
                      ? 'bg-gray-900 text-white'
                      : 'hover:bg-gray-100 text-gray-700'
                  }`}
                >
                  <FaFolder className="flex-shrink-0" />
                  <span className="truncate">{folder}</span>
                  <span className="ml-auto text-sm opacity-75">
                    {writings[folder].length}
                  </span>
                </button>
              </li>
            ))}
          </ul>
        </div>

        {/* Files Panel */}
        <div className="md:col-span-9">
          {selectedFolder && !selectedFile ? (
            <div className="bg-white/80 backdrop-blur-sm rounded-xl shadow-lg p-6">
              <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
                <FaFolder className="text-gray-600" />
                {selectedFolder}
              </h2>
              <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
                {writings[selectedFolder].map((file) => (
                  <button
                    key={file.id}
                    onClick={() => setSelectedFile(file)}
                    className="p-4 rounded-lg bg-white shadow-md hover:shadow-lg transition-all duration-200 text-left group"
                  >
                    <div className="flex items-start gap-3">
                      <FaFile className="flex-shrink-0 mt-1 text-gray-400 group-hover:text-gray-600" />
                      <div className="flex-1 min-w-0">
                        <h3 className="font-medium text-gray-900 truncate">
                          {file.title}
                        </h3>
                        <p className="text-sm text-gray-500 flex items-center gap-2 mt-2">
                          <FaCalendar className="flex-shrink-0" />
                          {new Date(file.date).toLocaleDateString()}
                        </p>
                      </div>
                    </div>
                  </button>
                ))}
              </div>
            </div>
          ) : selectedFile ? (
            <article className="bg-white/80 backdrop-blur-sm rounded-xl shadow-lg p-6 animate-fade-in">
              <button
                onClick={() => setSelectedFile(null)}
                className="text-sm text-gray-500 hover:text-gray-700 mb-4 flex items-center gap-2"
              >
                ‚Üê Back to {selectedFolder}
              </button>
              <div className="prose prose-lg max-w-none">
                <div className="flex items-center justify-between mb-6">
                  <h1 className="flex items-center gap-3 m-0">
                    {selectedFile.sticker && (
                      <span className="text-2xl">{selectedFile.sticker}</span>
                    )}
                    {selectedFile.title}
                  </h1>
                  <time className="text-sm text-gray-500 flex items-center gap-2">
                    <FaClock />
                    {new Date(selectedFile.date).toLocaleDateString()}
                  </time>
                </div>
                <div className="mt-8">{selectedFile.content}</div>
              </div>
            </article>
          ) : (
            <div className="flex items-center justify-center h-full text-gray-500">
              Select a folder to view its contents
            </div>
          )}
        </div>
      </div>
    </main>
  );
}